*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

html {
  height: 100%;
}

body {
  min-height: 100%;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
  color: inherit;
  font-style: normal;
}

a:hover {
  text-decoration: none;
}

@font-face {
  font-family: "Inter";
  src: url("../../fonts/Inter-Regular.woff2") format("woff2"), url("../../fonts/Inter-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Inter";
  src: url("../../fonts/Inter-SemiBold.woff2") format("woff2"), url("../../fonts/Inter-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
}
.body {
  font-family: "Inter", sans-serif;
  display: flex;
  flex-direction: column;
  padding: 50px 0px 0px 0px;
  background-image: url("./../../img/background.webp");
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.header {
  justify-items: center;
}
.header__title {
  font-size: 42px;
  font-weight: 600;
  margin: 0px 0px 10px 0px;
}
.header__link {
  font-size: 20px;
  font-weight: 600;
  text-decoration: none;
}
.header__link:hover {
  text-decoration: underline;
}

.main-content {
  margin: 40px 0px 0px 0px;
  flex: 1 1 auto;
}

.actions {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin: 0px 0px 50px 0px;
  position: relative;
}
.actions__form {
  display: flex;
  gap: 20px;
}

.form-actions__input,
.form-edit-modal__input {
  font-size: 15px;
  padding: 0px 5px;
}

.error {
  position: absolute;
  top: 34px;
  background-color: rgba(253, 119, 153, 0.541);
  border: 2px solid rgb(219, 23, 72);
  border-radius: 5px;
  height: 25px;
  padding: 0px 15px;
  display: flex;
  align-items: center;
  visibility: hidden;
}

.error-visible {
  visibility: visible;
  animation: fadeIn 5s;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.contact-list-wrapper {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.contact-list {
  width: 325px;
}
.contact-list__counter {
  display: none;
}
.contact-list__item {
  font-size: 22px;
  font-weight: 600;
  line-height: 204.54%;
  text-transform: uppercase;
  min-height: auto;
  background-color: rgb(197, 233, 196);
  border-top: 1px solid rgb(71, 150, 69);
  border-bottom: 1px solid rgb(71, 150, 69);
  padding: 0px 35px 0px 15px;
  display: flex;
  justify-content: space-between;
}
.contact-list__item[data-active=true] {
  background-color: rgb(136, 209, 135);
}
.contact-list__item[data-active=true] .contact-list__counter {
  display: block;
}

.contacts__item {
  display: flex;
  justify-content: space-between;
  gap: 10px;
}

.item-contacts {
  padding: 5px 10px;
  background-color: rgb(243, 250, 139);
  border-bottom: 1px solid rgb(255, 208, 1);
  border-top: 1px solid rgb(255, 208, 1);
  font-size: 16px;
}
.item-contacts__info {
  flex-grow: 1;
}
.item-contacts_with-extra-padding {
  padding-right: 50px;
}

.search-modal {
  background-color: rgb(255, 255, 255);
  width: 700px;
  height: 500px;
  border-radius: 15px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.search-modal__header {
  display: flex;
  width: 100%;
}
.search-modal__title {
  font-size: 45px;
  font-weight: 600;
  flex-grow: 1;
  text-align: center;
  margin: 15px 0px 25px 0px;
}
.search-modal__body {
  width: 580px;
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
}
.search-modal__input {
  width: 580px;
  height: 35px;
  font-size: 20px;
  margin: 0px 0px 17px 0px;
  padding: 0px 10px 0px 10px;
}
.search-modal__contacts {
  flex: 1 1 auto;
  max-height: 300px;
  overflow-y: auto;
}
.search-modal__empty {
  font-size: 30px;
  color: rgb(128, 128, 128);
}

.search-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  visibility: hidden;
  z-index: 1000;
  opacity: 0;
  transition: opacity 0.5s ease, visibility 0.5s ease;
  pointer-events: none;
}

.search-modal-overlay.active {
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.edit-modal {
  border-radius: 7px;
  background-color: rgb(245, 248, 248);
  padding: 8px;
  width: 320px;
  border: 2px solid rgb(0, 0, 0);
}
.edit-modal__header {
  display: flex;
  justify-content: space-between;
}
.edit-modal__title {
  font-weight: 600;
  font-size: 25px;
  flex-grow: 1;
  text-align: center;
  margin: 10px 0px 10px 0px;
}
.edit-modal__form {
  display: flex;
  flex-direction: column;
  gap: 13px;
  margin: 0px 15px 10px 15px;
}

.form-edit-modal__btn {
  margin: 0 auto;
}

.btn-icon {
  all: unset;
  cursor: pointer;
  font-size: 18px;
  transition: color 0.3s;
  align-self: center;
}
.btn-icon:hover {
  color: red;
}
.btn-icon_big {
  font-size: 23px;
  align-self: start;
}

.btn {
  font-weight: 600;
  font-size: 16px;
  padding: 3px 15px;
  border-radius: 10px;
  background-color: rgb(255, 255, 255);
  border: 2px solid rgb(118, 59, 196);
  transition: all 0.4s;
}
.btn:hover {
  color: rgb(255, 255, 255);
  background-color: rgb(135, 93, 190);
  border-color: rgb(255, 255, 255);
}

.input {
  border-radius: 6px;
  border: 2px solid rgb(190, 190, 190);
  transition: all 0.4s;
}
.input:hover, .input:focus {
  border-color: rgb(118, 59, 196);
}

.edit-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.5s ease, visibility 0.5s ease;
  pointer-events: none;
}

.edit-modal-overlay.active {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}